.recipe {
	border-radius: 10px;
	box-shadow: 0px 5px 20px rgb(71, 71, 71);
	margin: 20px;
	display: flex;
	flex-direction:column;
	justify-content: space-around;
	background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);;
	align-items: center;
	min-width: 40%;
}

.image{
	border-radius: 50%;
	width: 150px;
	height: 150px;
}

.calories{
	font-size: 14px;
	font-weight: bold;
}